import React, { Component } from 'react';
import Draggable from 'react-draggable';
import { ReactComponent as Bitcoin } from './assets/bitcoinn.svg';
import { ReactComponent as Nem } from './assets/nem.svg';
import { ReactComponent as Ether } from './assets/ethereum.svg';
import { ReactComponent as Dash } from './assets/dash.svg';
import { ReactComponent as Eos } from './assets/eos.svg';
import tableCell from './assets/circle.svg';
import style from './css/crypto.module.css';

class JoinCryptocurrencies extends Component {
	constructor(props) {
		super(props);
		this.state = {
			cryptoSelected: null,
			pairs: {
				bitcoin: false,
				nem: false,
				ether: false,
				dash: false,
				eos: false
			},
			showScore: false
		};
	}

	selectCrypto = crypto => {
		this.setState({
			cryptoSelected: crypto
		});
	};

	pairUp = isAPair => {
		const newPair = this.state.pairs;
		if (this.state.cryptoSelected === 'bitcoin') newPair.bitcoin = isAPair;
		else if (this.state.cryptoSelected === 'nem') newPair.nem = isAPair;
		else if (this.state.cryptoSelected === 'ether') newPair.ether = isAPair;
		else if (this.state.cryptoSelected === 'dash') newPair.dash = isAPair;
		else if (this.state.cryptoSelected === 'eos') newPair.eos = isAPair;
		this.setState({
			pairs: newPair
		});
	};

	showScore = () => {
		this.setState({
			showScore: true
		});
	};

	countScore = () => {
		let score = 0;
		for (const coin in this.state.pairs) {
			if (this.state.pairs[coin] === true) score += 1;
		}
		return score;
	};

	playAgain = () => {
		this.setState({
			cryptoSelected: null,
			pairs: {
				bitcoin: false,
				nem: false,
				ether: false,
				dash: false,
				eos: false
			},
			showScore: false
		});
	};

	render() {
		return (
			<div>
				{!this.state.showScore ? (
					<div className={style.container}>
						<p className={style.header}>Match the cryptocurrency name with it's logo</p>
						<Logo
							select={this.selectCrypto}
							selected={this.state.cryptoSelected}
							pairUp={this.pairUp}
						/>
						<button onClick={() => this.showScore()} className={style.button}>
							Submit
						</button>
					</div>
				) : (
					<Score
						correct={this.countScore()}
						playAgain={this.playAgain}
						close={this.props.switchComponent}
					/>
				)}
			</div>
		);
	}
}

const Score = ({ correct, playAgain, close }) => (
	<div>
		<div className={style.score}>
			<div className={style.scoreText}>Score</div>
			<div>
				{correct}
				/5
			</div>
		</div>
		<a onClick={close}>
			<button className={style.back}>Back to games</button>
		</a>
		<button className={style.retry} onClick={playAgain}>
			Retry
		</button>
	</div>
);

class Logo extends Component {
	etherPos = {
		xMin: 0,
		xMax: 0,
		yMin: 0,
		yMax: 0
	};

	eosPos = {
		xMin: 0,
		xMax: 0,
		yMin: 0,
		yMax: 0
	};

	nemPos = {
		xMin: 0,
		xMax: 0,
		yMin: 0,
		yMax: 0
	};

	dashPos = {
		xMin: 0,
		xMax: 0,
		yMin: 0,
		yMax: 0
	};

	bitcoinPos = {
		xMin: 0,
		xMax: 0,
		yMin: 0,
		yMax: 0
	};

	bitcoinLogoPos = {
		x: 0,
		y: 0
	};

	dashLogoPos = {
		x: 0,
		y: 0
	};

	nemLogoPos = {
		x: 0,
		y: 0
	};

	etherLogoPos = {
		x: 0,
		y: 0
	};

	eosLogoPos = {
		x: 0,
		y: 0
	};

	constructor(props) {
		super(props);
		this.state = {
			cryptoPosition: { x: 0, y: 0 }
		};
	}

	componentDidMount() {
		this.findCoinsPosition();
	}

	findTitlesPosition = (bitcoin, dash, ether, nem, eos) => {
		this.bitcoinPos = bitcoin;
		this.dashPos = dash;
		this.etherPos = ether;
		this.nemPos = nem;
		this.eosPos = eos;
	};

	findCoinsPosition = () => {
		this.bitcoinLogoPos.x =
			document.getElementById('bitcoin').getBoundingClientRect().x + window.scrollX;
		this.bitcoinLogoPos.y =
			document.getElementById('bitcoin').getBoundingClientRect().y + window.scrollY;

		this.dashLogoPos.x =
			document.getElementById('dash').getBoundingClientRect().x + window.scrollX;
		this.dashLogoPos.y =
			document.getElementById('dash').getBoundingClientRect().y + window.scrollY;

		this.nemLogoPos.x =
			document.getElementById('nem').getBoundingClientRect().x + window.scrollX;
		this.nemLogoPos.y =
			document.getElementById('nem').getBoundingClientRect().y + window.scrollY;

		this.etherLogoPos.x =
			document.getElementById('ether').getBoundingClientRect().x + window.scrollX;
		this.etherLogoPos.y =
			document.getElementById('ether').getBoundingClientRect().y + window.scrollY;

		this.eosLogoPos.x =
			document.getElementById('eos').getBoundingClientRect().x + window.scrollX;
		this.eosLogoPos.y =
			document.getElementById('eos').getBoundingClientRect().y + window.scrollY;
	};

	onStart = () => {
		// this.setState(prevState => ({ activeDrags: prevState.activeDrags + 1 }));
	};

	onStop = () => {
		// this.setState(prevState => ({ activeDrags: prevState.activeDrags - 1 }));
	};

	setPosition = (e, position) => {
		const { x, y } = position;
		this.setState({ cryptoPosition: { x, y } });
	};

	checkForPairs = position => {
		if (this.props.selected === 'bitcoin') {
			if (
				this.bitcoinLogoPos.x + position.x > this.bitcoinPos.xMin &&
				this.bitcoinLogoPos.x + position.x + 150 < this.bitcoinPos.xMax &&
				this.bitcoinLogoPos.y + position.y > this.bitcoinPos.yMin &&
				this.bitcoinLogoPos.y + position.y + 150 < this.bitcoinPos.yMax
			)
				this.props.pairUp(true);
			else this.props.pairUp(false);
		}
		if (this.props.selected === 'dash') {
			if (
				this.dashLogoPos.x + position.x > this.dashPos.xMin &&
				this.dashLogoPos.x + position.x + 150 < this.dashPos.xMax &&
				this.dashLogoPos.y + position.y > this.dashPos.yMin &&
				this.dashLogoPos.y + position.y + 150 < this.dashPos.yMax
			)
				this.props.pairUp(true);
			else this.props.pairUp(false);
		}
		if (this.props.selected === 'nem') {
			if (
				this.nemLogoPos.x + position.x > this.nemPos.xMin &&
				this.nemLogoPos.x + position.x + 150 < this.nemPos.xMax &&
				this.nemLogoPos.y + position.y > this.nemPos.yMin &&
				this.nemLogoPos.y + position.y + 150 < this.nemPos.yMax
			)
				this.props.pairUp(true);
			else this.props.pairUp(false);
		}
		if (this.props.selected === 'ether') {
			if (
				this.etherLogoPos.x + position.x > this.etherPos.xMin &&
				this.etherLogoPos.x + position.x + 150 < this.etherPos.xMax &&
				this.etherLogoPos.y + position.y > this.etherPos.yMin &&
				this.etherLogoPos.y + position.y + 150 < this.etherPos.yMax
			)
				this.props.pairUp(true);
			else this.props.pairUp(false);
		}
		if (this.props.selected === 'eos') {
			if (
				this.eosLogoPos.x + position.x > this.eosPos.xMin &&
				this.eosLogoPos.x + position.x + 150 < this.eosPos.xMax &&
				this.eosLogoPos.y + position.y > this.eosPos.yMin &&
				this.eosLogoPos.y + position.y + 150 < this.eosPos.yMax
			)
				this.props.pairUp(true);
			else this.props.pairUp(false);
		}
	};

	onControlledDragStop = (e, position) => {
		this.checkForPairs(position);
		const { x, y } = position;
		this.setState({ cryptoPosition: { x, y } });
		this.props.select('');
		this.setState({ cryptoPosition: { x: 0, y: 0 } });
		this.onStop();
	};

	render() {
		const dragHandlers = { onStart: this.onStart, onStop: this.onStop };
		const { cryptoPosition } = this.state;
		return (
			<div className={style.main}>
				<Title find={this.findTitlesPosition} />
				<div className={style.logos}>
					<Draggable
						position={this.props.selected === 'bitcoin' ? cryptoPosition : null}
						{...dragHandlers}
						onStop={this.onControlledDragStop}
						onMouseDown={() => this.props.select('bitcoin')}
						onStart={this.setPosition}
					>
						<Bitcoin />
					</Draggable>
					<Draggable
						position={this.props.selected === 'dash' ? cryptoPosition : null}
						{...dragHandlers}
						onStop={this.onControlledDragStop}
						onMouseDown={() => this.props.select('dash')}
						onStart={this.setPosition}
					>
						<Dash />
					</Draggable>
					<Draggable
						position={this.props.selected === 'nem' ? cryptoPosition : null}
						{...dragHandlers}
						onStop={this.onControlledDragStop}
						onMouseDown={() => this.props.select('nem')}
						onStart={this.setPosition}
					>
						<Nem />
					</Draggable>
					<Draggable
						position={this.props.selected === 'ether' ? cryptoPosition : null}
						{...dragHandlers}
						onStop={this.onControlledDragStop}
						onMouseDown={() => this.props.select('ether')}
						onStart={this.setPosition}
					>
						<Ether />
					</Draggable>
					<Draggable
						position={this.props.selected === 'eos' ? cryptoPosition : null}
						{...dragHandlers}
						onStop={this.onControlledDragStop}
						onMouseDown={() => this.props.select('eos')}
						onStart={this.setPosition}
					>
						<Eos />
					</Draggable>
				</div>
			</div>
		);
	}
}

class Title extends Component {
	componentDidMount() {
		let coin = document.getElementById('bitcoinPlace'); // padaryt kad kodas nesikartotu
		const bitcoin = {
			xMin: coin.getBoundingClientRect().left + window.scrollX,
			xMax: coin.getBoundingClientRect().right + window.scrollX,
			yMin: coin.getBoundingClientRect().top + window.scrollY,
			yMax: coin.getBoundingClientRect().bottom + window.scrollY
		};
		coin = document.getElementById('dashPlace');
		const dash = {
			xMin: coin.getBoundingClientRect().left + window.scrollX,
			xMax: coin.getBoundingClientRect().right + window.scrollX,
			yMin: coin.getBoundingClientRect().top + window.scrollY,
			yMax: coin.getBoundingClientRect().bottom + window.scrollY
		};
		coin = document.getElementById('etherPlace');
		const ether = {
			xMin: coin.getBoundingClientRect().left + window.scrollX,
			xMax: coin.getBoundingClientRect().right + window.scrollX,
			yMin: coin.getBoundingClientRect().top + window.scrollY,
			yMax: coin.getBoundingClientRect().bottom + window.scrollY
		};
		coin = document.getElementById('nemPlace');
		const nem = {
			xMin: coin.getBoundingClientRect().left + window.scrollX,
			xMax: coin.getBoundingClientRect().right + window.scrollX,
			yMin: coin.getBoundingClientRect().top + window.scrollY,
			yMax: coin.getBoundingClientRect().bottom + window.scrollY
		};
		coin = document.getElementById('eosPlace');
		const eos = {
			xMin: coin.getBoundingClientRect().left + window.scrollX,
			xMax: coin.getBoundingClientRect().right + window.scrollX,
			yMin: coin.getBoundingClientRect().top + window.scrollY,
			yMax: coin.getBoundingClientRect().bottom + window.scrollY
		};

		this.props.find(bitcoin, dash, ether, nem, eos);
	}

	render() {
		return (
			<div className={style.titles}>
				<table className={style.table}>
					<tr>
						<th>
							<p>Ether</p>
						</th>
						<th>
							<p>NEM</p>
						</th>
						<th>
							<p>EOS</p>
						</th>
						<th>
							<p>DASH</p>
						</th>
						<th>
							<p>Bitcoin</p>
						</th>
					</tr>
					<tr>
						<td id="etherPlace">
							<img src={tableCell} />
						</td>
						<td id="nemPlace">
							<img src={tableCell} />
						</td>
						<td id="eosPlace">
							<img src={tableCell} />
						</td>
						<td id="dashPlace">
							<img src={tableCell} />
						</td>
						<td id="bitcoinPlace">
							<img src={tableCell} />
						</td>
					</tr>
				</table>
			</div>
		);
	}
}
export default JoinCryptocurrencies;
