import React, { Component } from 'react';
import style from './css/quiz.module.css';

class Quiz extends Component {
	constructor(props) {
		super(props);
		this.state = {
			question: {
				questionText: '',
				answers: Array(4).fill(''),
				correctAnswer: 0
			},
			questions: [
				{
					questionText: 'When was SpectroCoin founded?',
					answers: ['2011', '2012', '2013', '2014'],
					correctAnswer: 3,
					isUsed: false
				},
				{
					questionText: 'What animal does SpectroCoin use in its logo?',
					answers: ['A bull', 'A deer', 'An elk', 'A hog'],
					correctAnswer: 1,
					isUsed: false
				},
				{
					questionText:
						'What is the correct email address if you want to apply or learn more about open positions at SpectroCoin?',
					answers: [
						'apply@spectrocoin.com',
						'hello@spectrocoin.com',
						'careers@spectrocoin.com',
						'opportunities@spectrocoin.com'
					],
					correctAnswer: 3,
					isUsed: false
				},
				{
					questionText:
						'Which of the below definitions describes the SpectroCoin team’s culture the best?',
					answers: ['Constantly learning', 'Passionate', 'Team Players', 'All the above'],
					correctAnswer: 4,
					isUsed: false
				},
				{
					questionText:
						'Which of the SpectroCoin departments has the biggest number of open positions?',
					answers: ['IT', 'Marketing', 'Customer Service', 'Finance'],
					correctAnswer: 1,
					isUsed: false
				},
				{
					questionText:
						'How many currency options does SpectroCoin offer when exchanging\n' +
						'fiat and virtual currencies?',
					answers: ['2', '10', 'More than 20', 'More than 30'],
					correctAnswer: 3,
					isUsed: false
				},
				{
					questionText:
						'Which of the below cryptocurrencies can be exchanged using SpectroCoin?',
					answers: ['DASH', 'XEM', 'Bitcoin', 'All the above'],
					correctAnswer: 4,
					isUsed: false
				},
				{
					questionText:
						'SpectroCoin Prepaid Cards can be denominated in the following currencies:',
					answers: [
						'Euros (EUR) and U.S dollars (USD)',
						'Euros (EUR) and British pounds (GBP)',
						'British pounds (GBP) and U.S dollars (USD)',
						'Euros (EUR) only'
					],
					correctAnswer: 1,
					isUsed: false
				},
				{
					questionText: 'What type of blockchain wallets does SpectroCoin offer?',
					answers: [
						'Mobile and online wallets',
						'Single and shared-user wallets',
						'Investment and lend wallets',
						'All the above'
					],
					correctAnswer: 1,
					isUsed: false
				},
				{
					questionText: 'Which of the following is NOT a product/service of SpectroCoin?',
					answers: [
						'Prepaid Card',
						'Blockchain Wallet',
						'Cryptocurrency Exchange Platform',
						'Online Banking'
					],
					correctAnswer: 4,
					isUsed: false
				},
				{
					questionText:
						'Which of the below cryptocurrencies is the first one in terms of market capitalization?',
					answers: ['Ether', 'Stellar', 'Bitcoin', 'Litecoin'],
					correctAnswer: 3,
					isUsed: false
				},
				{
					questionText: 'Who is known as the inventor of Bitcoin?',
					answers: ['Satoshi Nakamoto', 'Elon Musk', 'Bill Gates', 'Donald Trump'],
					correctAnswer: 1,
					isUsed: false
				},
				{
					questionText: 'In which of the following countries Bitcoin is still illegal?',
					answers: ['Bolivia', 'Brazil', 'Russia', 'Turkey'],
					correctAnswer: 1,
					isUsed: false
				},
				{
					questionText: 'What year was Ether (ETH) initially released?',
					answers: ['2009', '2012', '2015', '2017'],
					correctAnswer: 3,
					isUsed: false
				},
				{
					questionText:
						'In which of the following countries the local law authorities have actually hunted down individual cryptocurrency users?',
					answers: ['India', 'Bangladesh', 'South Korea', 'South Africa'],
					correctAnswer: 2,
					isUsed: false
				}
			],
			step: 1,
			correctAnswers: 0,
			attempt: 0
		};
	}

	componentDidMount() {
		this.startState = this.state;
		this.loadQuestion();
	}

	startState;

	playAgain = () => {
		if (this.state.attempt === 1) this.setState(this.startState);
		else {
			this.setState(prevState => ({
				question: {
					questionText: '',
					answers: Array(4).fill(''),
					correctAnswer: 0
				},
				step: 1,
				correctAnswers: 0,
				attempt: prevState.attempt + 1
			}));
		}

		this.loadQuestion();
	};

	next = answer => {
		this.checkAnswer(answer);
		this.setState(prevState => ({
			step: prevState.step + 1
		}));
		this.loadQuestion();
	};

	checkAnswer = answer => {
		if (answer === this.state.question.correctAnswer)
			this.setState(prevState => ({
				correctAnswers: prevState.correctAnswers + 1
			}));
	};

	loadQuestion = () => {
		let random;
		do {
			random = Math.floor(Math.random() * 15);
		} while (this.state.questions[random].isUsed);
		const newQuestions = this.state.questions;
		newQuestions[random].isUsed = true;
		const newQuestion = {
			questionText: this.state.questions[random].questionText,
			answers: this.state.questions[random].answers,
			correctAnswer: this.state.questions[random].correctAnswer
		};
		this.setState({
			question: newQuestion,
			questions: newQuestions
		});
	};

	render() {
		return (
			<>
				{this.state.step <= 5 ? (
					<Question question={this.state.question} next={this.next} />
				) : (
					<div>
						<Score
							correct={this.state.correctAnswers}
							playAgain={this.playAgain}
							close={this.props.switchComponent}
						/>
					</div>
				)}
			</>
		);
	}
}

class Question extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selected: 0
		};
	}

	selectAnswer = answer => {
		this.setState({
			selected: answer
		});
	};

	render() {
		return (
			<div className={style.buttons}>
				<p className={style.question}>{this.props.question.questionText}</p>
				<button
					onClick={() => this.selectAnswer(1)}
					className={this.state.selected === 1 ? style.selected : style.button}
				>
					{this.props.question.answers[0]}
				</button>
				<button
					onClick={() => this.selectAnswer(2)}
					className={this.state.selected === 2 ? style.selected : style.button}
				>
					{this.props.question.answers[1]}
				</button>
				<button
					onClick={() => this.selectAnswer(3)}
					className={this.state.selected === 3 ? style.selected : style.button}
				>
					{this.props.question.answers[2]}
				</button>
				<button
					onClick={() => this.selectAnswer(4)}
					className={this.state.selected === 4 ? style.selected : style.button}
				>
					{this.props.question.answers[3]}
				</button>
				<button
					onClick={() => {
						this.setState({ selected: 0 });
						this.props.next(this.state.selected);
					}}
					className={style.next}
				>
					Next
				</button>
			</div>
		);
	}
}

const Score = ({ correct, playAgain, close }) => (
	<div>
		<div className={style.score}>
			<div className={style.scoreText}>Score</div>
			<div>
				{correct}
				/5
			</div>
		</div>

		<a onClick={close}>
			<button className={style.back}>Back to games</button>
		</a>
		<button className={style.retry} onClick={playAgain}>
			Retry
		</button>
	</div>
);

export default Quiz;
