import React, { Component } from 'react';
import './App.css';
import JoinCryptocurrencies from './JoinCryptocurrencies';
import FindWords from './FindWords';
import Quiz from './Quiz';
import logo from './assets/logo.svg';
import style from './css/app.module.css';

const Menu = ({ switchComponent }) => (
	<div className={style.buttons}>
		<a>
			<button className={style.button} onClick={() => switchComponent('join')}>
				Find a match
			</button>
		</a>
		<a>
			<button className={style.button} onClick={() => switchComponent('quiz')}>
				Questions
			</button>
		</a>
		<a>
			<button className={style.button} onClick={() => switchComponent('find')}>
				Find a word
			</button>
		</a>
	</div>
);

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			activeGame: null
		};
	}

	switchComponent = activeGame => {
		this.setState({
			activeGame: activeGame || null
		});
	};

	render() {
		let ActiveComponent;
		switch (this.state.activeGame) {
			case 'join':
				ActiveComponent = JoinCryptocurrencies;
				break;
			case 'find':
				ActiveComponent = FindWords;
				break;
			case 'quiz':
				ActiveComponent = Quiz;
				break;
			default:
				ActiveComponent = Menu;
		}

		return (
			<>
				<div className={style.logo}>
					<img onClick={this.switchComponent} src={logo} alt="" />
				</div>
				<ActiveComponent switchComponent={this.switchComponent} />
			</>
		);
	}
}

export default App;
